html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  line-height: 1.5;
  font-size: 16px;
  letter-spacing: 1px;
  background-color: rgba(0, 0, 0, 0.99);
}

::-webkit-scrollbar {
  width: 7px;
  background: rgba(0, 0, 0, 0.99);
}
@media (min-width: 1600px) {
  ::-webkit-scrollbar {
    width: 19px;
  }
}

::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background: #ea0f1e;
}
@media (min-width: 1600px) {
  ::-webkit-scrollbar-thumb {
    border-radius: 20px;
  }
}

a {
  text-decoration: none;
  color: #fff;
  font-size: 2.5rem;
  margin: 0;
}
@media (min-width: 1600px) {
  a {
    font-size: 7rem;
  }
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

h1 {
  color: #fff;
  font-size: 3.7rem;
}
@media (min-width: 360px) {
  h1 {
    font-size: 4.3rem;
  }
}
@media (min-width: 768px) {
  h1 {
    font-size: 9rem;
  }
}
@media (min-width: 1600px) {
  h1 {
    font-size: 15rem;
  }
}

h1,
h2 {
  text-align: center;
  text-transform: capitalize;
}

h2 {
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 3.5rem;
  }
}
@media (min-width: 1600px) {
  h2 {
    font-size: 10rem;
    margin-top: 0;
  }
}

h3 {
  color: #ea0f1e;
  font-size: 2.5rem;
  font-weight: 700;
  text-align: left;
}
@media (min-width: 1600px) {
  h3 {
    font-size: 8rem;
  }
}

p {
  font-size: 2rem;
}
@media (min-width: 1600px) {
  p {
    font-size: 5rem;
  }
}

.contenedor {
  width: 100%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (min-width: 360px) {
  .contenedor {
    width: 95%;
  }
}
@media (min-width: 1600px) {
  .contenedor {
    max-width: 200rem;
  }
}
.contenedor-small {
  width: 80%;
  margin: 0rem auto 0rem auto;
}
@media (min-width: 768px) {
  .contenedor-small {
    width: 57%;
    max-width: 185rem;
  }
}
@media (min-width: 1600px) {
  .contenedor-small {
    width: 65%;
  }
}

.display-flex-center {
  display: flex;
  justify-content: center;
}

@media (min-width: 992px) {
  .alinear-derecha {
    display: flex;
    justify-content: flex-start;
  }
}

.boton {
  background-color: #c40410;
  display: block;
  font-size: 2.5rem;
  border-radius: 3rem;
  padding: 1rem 2.5rem;
  align-items: center;
  text-align: center;
  margin: 3rem 0;
}
@media (min-width: 1600px) {
  .boton {
    font-size: 5rem;
  }
}
.boton:hover {
  background-color: #ea0f1e;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton {
    padding: 1.5rem 3.5rem;
  }
  .boton:hover {
    padding: 1.5rem 3.7rem;
  }
}

.alinear-botones {
  margin-bottom: 6rem;
}
@media (min-width: 992px) {
  .alinear-botones {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.boton-icono a {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.boton-icono a img {
  width: 3rem;
  margin-left: 0.5rem;
}

.header {
  height: auto;
}

.header-inferior {
  height: 55rem;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
@media (min-width: 768px) {
  .header-inferior {
    height: 60rem;
  }
}
@media (min-width: 1600px) {
  .header-inferior {
    height: 140rem;
  }
}

.webp .header-inferior {
  background-image: url(../img/Cristhian-Sevilla-R.webp);
  background-position: center center;
}
@media (min-width: 768px) {
  .webp .header-inferior {
    background-image: url(../img/Cristhian-Sevilla.webp);
    background-position: center;
  }
}

.no-web .header-inferior {
  background-image: url(../img/Cristhian-Sevilla-R.jpg);
  background-position: center center;
}
@media (min-width: 768px) {
  .no-web .header-inferior {
    background-image: url(../img/Cristhian-Sevilla.jpg);
    background-position: center;
  }
}

.header-texto {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem 3rem;
}
@media (min-width: 768px) {
  .header-texto {
    /*Alinea los elementos en vertical*/
    align-items: center;
    width: 100%;
    padding: 0;
  }
}
.header-texto h1 {
  font-size: 5rem;
  line-height: 1.5;
  text-align: left;
  max-width: 9rem;
}
.header-texto h1 span {
  color: #ea0f1e;
}
@media (min-width: 360px) {
  .header-texto h1 {
    font-size: 6rem;
  }
}
@media (min-width: 768px) {
  .header-texto h1 {
    line-height: 1.8;
    font-size: 8rem;
    text-align: unset;
    line-height: unset;
  }
}
@media (min-width: 992px) {
  .header-texto h1 {
    font-size: 9rem;
  }
}
@media (min-width: 1600px) {
  .header-texto h1 {
    font-size: 17.9rem;
  }
}

.wave {
  height: 10rem;
  width: 100%;
  background: rgba(0, 0, 0, 0.99);
}
.wave path {
  fill: #e1e1e1;
}
@media (min-width: 1600px) {
  .wave {
    height: 15rem;
  }
}

.menu {
  position: fixed;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.99);
  padding: 1.3rem;
  width: 100%;
  box-shadow: 0.1rem 0.2rem 0.3rem #333;
}
@media (min-width: 768px) {
  .menu {
    display: flex;
    justify-content: space-between;
    padding: 1.5rem 5rem;
  }
}
@media (min-width: 1600px) {
  .menu {
    padding: 3rem 14rem;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 3rem;
}
@media (min-width: 360px) {
  .mobile-menu img {
    width: 3.5rem;
  }
}

.logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo p {
  font-size: 2.8rem;
  text-align: left;
  padding-left: 0;
  color: #fff;
  text-transform: uppercase;
  margin: 0;
}
.logo p span {
  color: #ea0f1e;
  font-weight: 600;
}
@media (min-width: 360px) {
  .logo p {
    font-size: 3.5rem;
  }
}
@media (min-width: 1600px) {
  .logo p {
    font-size: 7rem;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.ocultar-menu {
  visibility: hidden;
  opacity: 0;
}

.navegacion {
  background: rgba(0, 0, 0, 0.94);
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  top: 0%;
  left: 0%;
  padding-top: 7rem;
  transition: all 1s ease;
}
@media (min-width: 768px) {
  .navegacion {
    top: unset;
    left: unset;
    width: unset;
    position: unset;
    display: flex;
    flex-direction: row;
    visibility: unset;
    opacity: 1;
    height: unset;
    padding-top: unset;
  }
}
.navegacion a {
  display: block;
  text-align: center;
  font-weight: 400;
  line-height: 1.8;
  padding: 0.5rem 0;
}
.navegacion a:hover {
  color: #ea0f1e;
}
@media (min-width: 360px) {
  .navegacion a {
    padding: 1rem 0;
  }
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    padding: unset;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
@media (min-width: 1600px) {
  .navegacion a {
    margin-right: 5rem;
  }
}

.uno {
  z-index: -10;
  -webkit-clip-path: circle(0% at 0 0);
  clip-path: circle(0% at 100% 0%);
}
@media (min-width: 768px) {
  .uno {
    clip-path: circle(300% at 0 0);
  }
}

.dos {
  z-index: -10;
  -webkit-clip-path: circle(73% at 0 0);
  clip-path: circle(80% at 90% 0%);
}
@media (min-width: 360px) {
  .dos {
    clip-path: circle(70% at 80% 0);
  }
}
@media (min-width: 768px) {
  .dos {
    clip-path: circle(300% at 0 0);
  }
}

.presentacion {
  background-color: #e1e1e1;
  padding: 0rem 1rem 1rem 1rem;
}
@media (min-width: 1600px) {
  .presentacion {
    padding: 7rem 0;
  }
}
.presentacion h2 {
  padding: 1.3rem 0 2.5rem 0;
  margin-bottom: 0;
  border-bottom: 1px solid #bdbdbd;
  font-size: 3rem;
  font-weight: bold;
}
.presentacion h2 span {
  color: #ea0f1e;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .presentacion h2 {
    font-size: 4rem;
    text-align: left;
    padding: 3rem;
    margin-top: 1em;
  }
}
@media (min-width: 1600px) {
  .presentacion h2 {
    font-size: 8rem;
    border-bottom: 2.5px solid #bdbdbd;
  }
}
.presentacion p {
  font-size: 2rem;
  padding: 1rem;
  margin-top: 0;
}
@media (min-width: 768px) {
  .presentacion p {
    text-align: left;
    line-height: 1.2;
    margin-top: 3rem;
  }
}
@media (min-width: 1600px) {
  .presentacion p {
    font-size: 4.5rem;
  }
}
.presentacion p span {
  font-weight: bold;
}

@media (min-width: 992px) {
  .presentacion-contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.footer p {
  padding: 1rem;
  color: #fff;
  text-align: center;
  font-size: 1.8rem;
}
@media (min-width: 360px) {
  .footer p {
    font-size: 2rem;
  }
}
.footer p span {
  color: #ea0f1e;
  font-weight: 600;
}

.wave-footer {
  height: 13rem;
  width: 100%;
  background-color: #e1e1e1;
}
@media (min-width: 1600px) {
  .wave-footer {
    height: 15rem;
  }
}

.display-flex-center p {
  text-align: center;
  color: #ea0f1e;
  margin-bottom: 4rem;
  font-size: 2.3rem;
  padding: 0 0.5rem;
}
@media (min-width: 360px) {
  .display-flex-center p {
    font-size: 2.5rem;
    margin-bottom: 5rem;
    padding: unset;
  }
}
@media (min-width: 768px) {
  .display-flex-center p {
    font-size: 3rem;
  }
}
@media (min-width: 1600px) {
  .display-flex-center p {
    font-size: 8rem;
    margin-bottom: 8rem;
  }
}
.display-flex-center p span {
  color: #ea0f1e;
}

.contenedor-imagen {
  width: 100%;
  max-width: 100rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-imagen {
    width: 60%;
    max-width: 100rem;
  }
}

.descripcion {
  margin-bottom: 4rem;
  color: #fff;
}
.descripcion h2 {
  text-align: left;
  color: #ea0f1e;
}
@media (min-width: 1600px) {
  .descripcion h2 {
    margin: 8rem 0;
  }
}
.descripcion p {
  font-size: 1.6rem;
}
@media (min-width: 360px) {
  .descripcion p {
    font-size: 1.8rem;
  }
}
@media (min-width: 1600px) {
  .descripcion p {
    font-size: 5rem;
  }
}

.habilidades ul {
  font-size: 1.6rem;
  color: #fff;
}
@media (min-width: 360px) {
  .habilidades ul {
    font-size: 1.8rem;
  }
}
@media (min-width: 1600px) {
  .habilidades ul {
    font-size: 5rem;
  }
}
@media (min-width: 768px) {
  .habilidades {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    margin-bottom: 4rem;
  }
}

.contacto-enlace {
  display: flex;
  color: #ea0f1e;
  font-size: 1.7rem;
  margin: 1rem 0;
  padding: 0.5rem 0;
}
@media (min-width: 360px) {
  .contacto-enlace {
    font-size: 2rem;
    padding: 1rem 0 1rem 2rem;
  }
}
@media (min-width: 768px) {
  .contacto-enlace:hover {
    margin-left: 40px;
    transition: all 0.5s cubic-bezier(0.2, 0.6, 0.2, 1);
  }
}
@media (min-width: 1600px) {
  .contacto-enlace {
    font-size: 5.5rem;
    margin: 6rem 0;
  }
}

.enlace-icon {
  width: 3rem;
  padding-right: 0.5rem;
}
@media (min-width: 360px) {
  .enlace-icon {
    width: 4.3rem;
    padding-right: 1.5rem;
  }
}
@media (min-width: 1600px) {
  .enlace-icon {
    width: 8rem;
  }
}
.enlace-icon img {
  filter: invert(100%);
}

.portafolio h2 {
  margin: 0;
  color: #fff;
}
@media (min-width: 768px) {
  .portafolio .proyectos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3.5rem;
  }
}
@media (min-width: 1600px) {
  .portafolio .proyectos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 8rem;
  }
}
.portafolio .proyecto {
  margin: 3rem 0;
  box-shadow: 0 0 4px 1px #333;
  padding: 1rem;
  border-radius: 1.5rem;
  align-items: center;
}
.portafolio .proyecto p {
  margin: 0 0 6rem 0;
  font-size: 1.8rem;
  margin-bottom: 1rem;
  color: #ea0f1e;
  text-align: center;
}
@media (min-width: 768px) {
  .portafolio .proyecto p {
    font-size: 3rem;
  }
}
@media (min-width: 1600px) {
  .portafolio .proyecto p {
    font-size: 5.5rem;
  }
}
.portafolio .proyecto .proyecto-imagen {
  margin: 0 auto;
}
.portafolio .proyecto .proyecto-imagen a {
  margin-top: 2rem;
  border-radius: 1rem;
  padding: 0.7rem;
}
@media (min-width: 1600px) {
  .portafolio .proyecto .proyecto-imagen a {
    margin-top: 4rem;
    border-radius: 3rem;
